export default {
	data() {
		return {
			info:'',
		};
	},
	methods:{
		aboutUs(){
			this.$Request.get(`/appSystemController.do?aboutUs`)
			.then(res => {
				if(res.code == 0 || res.code == 150){
					this.info = res.data.aboutUs.replace(/\<img/gi, '<img style="max-width:100%;height:auto" ')
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		}
	}
}
